import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Urban(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/urban.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[6.55, 3.09, 6.21]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere161.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere161_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere161_2.geometry}
          material={materials.outline}
        />
      </group>
    </group>
  );
}
export default Urban;
useGLTF.preload("/glossarySelected/urban.glb");
